.similar {
    margin-top: 50px;
    margin-bottom: 80px;
    &__inner {
    }

    &__title {
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        color: rgb(38, 38, 38);
        margin-bottom: 10px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        @media (max-width: 1200px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 700px) {
            grid-template-columns: 1fr;
        }
    }

    &__items {
        cursor: pointer;
        background-color: rgb(255, 255, 255);
        border-radius: 0px;
        box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 10px 0px;
        &:hover {
            .similar__img {
                background-size: cover;
                background-repeat: no-repeat;
                background-image: linear-gradient(rgba($red, 0.15), rgba($red, 0.15)),
                    url("https://d1muf25xaso8hp.cloudfront.net/https%3A%2F%2Fs3.amazonaws.com%2Fappforest_uf%2Ff1618183402804x163200676725006720%2Fhttps___s3.amazonaws.com_appforest_uf_f1613590193470x146190636333550100_miz-medi-hospital_95.jpg?w=&h=&auto=compress&dpr=1&fit=max");
            }
            .similar__name {
                color: $red;
            }
        }
    }

    &__img {
        width: 100%;
        height: 298px;
        background-color: rgba(255, 255, 255, 0);
        background-repeat: no-repeat    ;
        background-position: center center;
        background-size: cover;
        border-radius: 0px;
        transition: background 200ms ease 0s;
        background-image: url("https://d1muf25xaso8hp.cloudfront.net/https%3A%2F%2Fs3.amazonaws.com%2Fappforest_uf%2Ff1618183402804x163200676725006720%2Fhttps___s3.amazonaws.com_appforest_uf_f1613590193470x146190636333550100_miz-medi-hospital_95.jpg?w=&h=&auto=compress&dpr=1&fit=max");
    }

    &__stars {
        margin-top: 10px;
        margin-bottom: 5px;
        padding: 0 10px;
        color: rgb(255, 198, 98);
    }

    &__name {
        padding: 0 10px;
        white-space: pre-wrap;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        color: rgb(41, 41, 41);
        transition: color 200ms ease 0s;
    }

    &__locate {
        padding: 0 10px;
        display: flex;
        margin-top: 10px;
    }

    &__locate-icon {
        height: 16px;
        width: 16px;
    }

    &__locate-text {
        margin-left: 10px;
    }

    &__bot {
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }

    &__bot-img {
        height: 16px;
        width: 16px;
    }

    &__like {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 33px;
        background: none rgba(71, 157, 131, 0.4);
        border-radius: 80px;
        transition: background 200ms ease 0s;
        width: 33px;
        border: none;
        &:hover {
            background: none rgba(71, 157, 131, 1);
        }
    }
}
