.intro {
    margin-top: 75px;
    padding-top: 10px;
    &__photo {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 10px;
        row-gap: 10px;
        @media (max-width: 950px){
            grid-template-columns: 1fr;
        }
    }

    &__photo-items {
        width: 100%;
        &:last-child {
            grid-column: 1/2;
            grid-row: 1/3;
            @media (max-width: 950px){
                grid-column: 1/3;
                grid-row: 1/2;
            }
            @media (max-width: 950px){
                grid-row: 1/2;
                grid-column: 1/2;
            }
        }
    }
}
#intro__photo1 {
    cursor: pointer;
    box-sizing: border-box;
    z-index: 2;
    height: 246px;
    background-color: rgba(255, 255, 255, 0);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-radius: 0px;
    width: 100%;
    background-image: url("https://d1muf25xaso8hp.cloudfront.net/https%3A%2F%2Fs3.amazonaws.com%2Fappforest_uf%2Ff1618182855424x596522840986929700%2F%25D0%2591%25D0%25B5%25D0%25B7%2520%25D0%25BD%25D0%25B0%25D0%25B7%25D0%25B2%25D0%25B0%25D0%25BD%25D0%25B8%25D1%258F.jpeg?w=&h=&auto=compress&dpr=1&fit=max");
}
