.header {
    background: white;
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid rgb(230, 230, 230);
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 75px;
    }

    &__left {
        display: flex;
        align-items: center;
    }

    &__logo {
        height: 35px;
        width: 35px;
    }

    &__name {
        box-sizing: border-box;
        z-index: 27;
        height: 37px;
        width: 176.22px;
        white-space: pre-wrap;
        overflow: visible;
        padding-bottom: 0px;
        font-family: Barlow;
        font-size: 25px;
        font-weight: 800;
        color: rgb(36, 36, 36);
        line-height: 1.4;
        padding-top: 2px;
        border-radius: 0px;
        transition: color 100ms ease 0s;
    }

    &__nav {
        display: flex;
        gap: 35px;
        @media(max-width: 1000px){
            display: none;
        }
    }

    &__nav-items {
        width: 110px;
    }

    &__nav-title {
        box-sizing: border-box;
        white-space: pre-wrap;
        overflow: visible;
        padding-bottom: 0px;
        font-family: Barlow;
        font-size: 16px;
        font-weight: 600;
        color: rgb(36, 36, 36);
        text-shadow: rgba(66, 133, 244, 0) 0px 0px 0px;
        line-height: 1.5;
        padding-top: 7.5px;
        border-radius: 0px;
        transition: color 100ms ease 0s;
        cursor: pointer;
        text-decoration: none;
        font-style: normal;
        text-align: left;
        margin-bottom: 3px;
        &:hover {
            color: rgb(71, 157, 131);
        }
    }

    &__nav-description {
        box-sizing: border-box;
        white-space: pre-wrap;
        overflow: visible;
        padding-bottom: 0px;
        font-family: Barlow;
        font-size: 10px;
        color: rgb(148, 148, 148);
        line-height: 1;
        border-radius: 0px;
    }

    &__contact {
        display: flex;
        gap: 10px;
        @media(max-width: 1000px){
            display: none;
        }
    }

    &__contact-icon {
        height: 30px;
        width: 30px;
    }

    &__ava {
        box-sizing: border-box;
        z-index: 2;
        height: 40px;
        width: 40px;
        background-color: rgba(255, 255, 255, 0);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        border-style: solid;
        border-width: 2px;
        border-color: rgb(255, 109, 105);
        border-radius: 80px;
        cursor: pointer;
        background-image: url("https://d1muf25xaso8hp.cloudfront.net/https%3A%2F%2Fs3.amazonaws.com%2Fappforest_uf%2Ff1611680290882x595237282026621600%2Fdefault_user.png?w=48&h=&auto=compress&dpr=1&fit=max");
        @media(max-width: 1000px){
            display: none;
        }
    }
}
