.burger {
    $buger: &;
    --burger-width: 40px;
    --burger-height: 20px;
    --burger-line-height: 4px;
    display: none;
    @include burger;

    &__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: fixed;
        top: 0;
        left: -100%;
        transition: left 0.3s ease-out;
        height: 100vh;
        background: rgb(34, 34, 34);
        width: 300px;   
        z-index: 2003;
        font-family: Lato;
        font-size: 16px;
        font-weight: normal;
        text-decoration: none;
        font-style: normal;
        color: rgb(232, 232, 232);
    }
    &__items{
        width: 100%;
        position: relative;
        min-height: 50px;
        padding-left: 20px;
        transition: color 0.1s linear;
        display: flex;
        align-items: flex-start;
        align-items: center;
        &:hover{
            color: $red;
        }
        &::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 1px;
            background: rgba(#fff,0.3);
            bottom: 0;
            left: 0;
        }
    }
    &--active {
        #{$buger}__list {
            left: 0;
        }
    }
    @media (max-width: 1000px) {
        display: block;
    }
}
