.footer {
    padding-top: 50px;
    padding-bottom: 30px;
    background: $green;
    &__inner {
    }

    &__top {
        position: relative;
        display: grid;
        grid-template-columns: repeat(5,1fr );
        gap: 20px;
        padding-bottom: 80px;
        row-gap: 30px;
        &::after {
            display: block;
            content: "";
            width: 100vw;
            height: 1px;
            background: rgba(#fff, 0.5);
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            bottom: 0;
        }
        @media (max-width: 830px){
            grid-template-columns: repeat(4,1fr );
        }
        @media (max-width: 700px){
            grid-template-columns: repeat(3,1fr );
        }
        @media (max-width: 580px){
            grid-template-columns: repeat(2,1fr );
        }
    }

    &__items {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        @media (max-width: 450px) {
            width: calc((100% - 50px) / 2);
        }
        &--logo {
            @media (max-width: 500px) {
                grid-column: 1/-1;
            }
        }
    }

    &__logo {
        font-family: Barlow;
        font-size: 27px;
        font-weight: bold;
        color: rgb(255, 255, 255);
    }

    &__text {
        font-family: Barlow;
        font-size: 10px;
        font-weight: 600;
        color: rgb(255, 255, 255);
        min-width: 300px;
    }

    &__title {
        margin-bottom: 10px;
        font-family: Barlow;
        font-size: 18px;
        font-weight: 600;
        color: rgb(255, 255, 255);
        min-width: 150px;
    }

    &__link {
        margin-top: 10px;
        min-width: 150px;
        user-select: none;
        cursor: pointer;
        pointer-events: auto;
        font-family: Barlow;
        font-size: 14px;
        font-weight: 500;
        color: rgb(255, 255, 255);
        &:hover {
            text-decoration: underline;
        }
    }

    &__bot {
        margin-top: 20px;
        display: grid;
        grid-template-columns: repeat(4,1fr );
        gap: 20px;
        @media (max-width: 650px) {
            justify-content: center;
            grid-template-columns: repeat(3,1fr );
        }
        @media (max-width: 500px) {
            justify-content: center;
            grid-template-columns: repeat(2,1fr );
        }
    }
    &__bot-title {
        user-select: none;
        cursor: pointer;
        pointer-events: auto;
        font-family: Barlow;
        font-size: 14px;
        font-weight: 500;
        color: rgb(255, 255, 255);
        white-space: nowrap;
    }
}
