.review {
    background-color: rgba(255, 255, 255, 0);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-radius: 0px;
    background-image: linear-gradient(rgba(71, 157, 131, 0.93), rgba(71, 157, 131, 0.93)),
        url("https://d1muf25xaso8hp.cloudfront.net/https%3A%2F%2Fs3.amazonaws.com%2Fappforest_uf%2Ff1601328281822x809052214689090400%2Fproduct-design-line-angle-poster-background-png-clip-art.png?w=1536&h=&auto=compress&dpr=1&fit=max");
    &__inner {
        display: flex;
        gap: 30px;
        padding: 50px 0;
        @media (max-width: 610px) {
            flex-direction: column;
        }
    }

    &__left {
        display: flex;
        flex-direction: column;
        @media (max-width: 610px) {
            align-items: flex-start;
        }
    }

    &__ava {
        height: 160px;
        width: 160px;
        border-style: solid;
        border-width: 2px;
        border-color: rgb(224, 224, 224);
        border-radius: 80px;
        background-color: rgba(255, 255, 255, 0);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        background-image: url("https://d1muf25xaso8hp.cloudfront.net/https%3A%2F%2Fs3.amazonaws.com%2Fappforest_uf%2Ff1618186497228x476138238989838800%2FIMG_4196.JPG?w=192&h=&auto=compress&dpr=1&fit=max");
    }

    &__left-bot {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 10px;
        font-family: Roboto;
        font-size: 14px;
        color: rgb(255, 255, 255);
        text-align: center;
        font-weight: 500;
        @media (max-width: 610px) {
            margin-left: 40px;
        }
    }

    &__left-text {
    }

    &__right {
    }

    &__name {
        font-family: Roboto;
        font-size: 30px;
        font-weight: 700;
        color: rgb(255, 255, 255);
    }

    &__right-text {
        margin-top: 30px;
        font-weight: 500;
        font-family: Roboto;
        font-size: 16px;
        color: rgb(255, 255, 255);
    }

    &__right-btn {
        margin-top: 20px;
        height: 42px;
        width: 228px;
        cursor: pointer;
        background: none rgba(255, 255, 255, 0);
        border: 1px solid rgb(255, 255, 255);
        text-align: center;
        font-family: Roboto;
        font-size: 15px;
        font-weight: 700;
        color: rgb(255, 255, 255);
        line-height: 1;
        border-radius: 20px;
        transition: color 200ms ease 0s, background 200ms ease 0s;
        text-decoration: none;
        &:hover {
            background-color: #fff;
            color: #ff6d69;
        }
    }
}
