.content {
    margin-top: 10px;
    margin-bottom: 80px;
    .container {
    }

    &__inner {
        display: grid;
        grid-template-columns: 1fr 300px;
        column-gap: 30px;
        @media (max-width: 800px) {
            grid-template-columns: 1fr;
        }
    }

    &__left {
        margin-top: 30px;
    }

    &__stars {
        color: rgba(255, 200, 97, 0.5);
        font-size: 150%;
        cursor: pointer;
    }

    &__name {
        box-sizing: border-box;
        z-index: 3;
        white-space: pre-wrap;
        overflow: visible;
        font-family: Roboto;
        font-size: 24px;
        font-weight: 700;
        color: rgb(41, 41, 41);
        line-height: 1.2;
        padding-top: 2px;
        border-radius: 0px;
        padding-bottom: 0px;
    }

    &__location {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    &__location-link {
        box-sizing: border-box;
        z-index: 4;
        white-space: pre-wrap;
        overflow: visible;
        font-family: Roboto;
        font-size: 16px;
        color: rgb(138, 143, 154);
        line-height: 1.4;
        padding-top: 1px;
        border-radius: 0px;
        padding-bottom: 0px;
        transition: color 0.1s linear;
        &:hover {
            color: red;
        }
    }

    &__title {
        box-sizing: border-box;
        z-index: 5;
        white-space: pre-wrap;
        overflow: visible;
        padding-bottom: 0px;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 700;
        color: rgb(36, 36, 36);
        line-height: 1.1;
        border-radius: 0px;
    }

    &__description {
        z-index: 6;
        overflow: visible;
        font-family: Roboto;
        font-size: 14px;
        color: rgb(93, 100, 117);
        line-height: 1.9;
        border-radius: 0px;
        padding-bottom: 0px;
        margin-top: 10px;
    }

    &__block {
        box-sizing: border-box;
        background-color: rgb(255, 255, 255);
        border-radius: 3px;
        box-shadow: rgba(181, 127, 250, 0.1) 0px 2px 12px 0px;
        margin: 30px 0;
        padding: 30px;
    }

    &__block-title {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 15px;
    }

    &__block-content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        row-gap: 10px;
        column-gap: 10px;
    }

    &__block-text {
        font-family: Roboto;
        font-size: 14px;
        color: rgb(41, 41, 41);
        border-radius: 0px;
        padding-bottom: 0px;
        span {
            font-weight: 900;
        }
        &--flex{
            display: flex;
        }
    }

    &__block-content-img {
        margin-right: 5px;
    }

    &__block-content-3 {
        padding: 10px;
        display: flex;
        gap: 30px;
        border-style: solid;
        border-width: 1px;
        border-color: rgb(219, 219, 219);
    }
    &__block-content-3-ava {
        height: auto;
        
        @media (max-width: 415px) {
            width: 100%;
            max-width: 100%;
            min-width: 0;
        }
    }
    &__block-content-3-text-title {
        min-width: 142px;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 900;
        color: rgb(41, 41, 41);
        margin-bottom: 10px;
        margin-top: 5px;
    }

    &__block-content-3-text-description {
        min-width: 130px;
        font-family: Roboto;
        font-size: 14px;
        color: rgb(93, 100, 117);
        line-height: 1.5;
    }

    &__map {
        width: 100%;
        height: 300px;
        background-color: black;
    }

    &__right {
        @media (max-width: 800px) {
            margin-top: 30px;
        }
    }

    &__right-averae {
        background-color: $green;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-family: Roboto;
        font-size: 20px;
        color: rgb(255, 255, 255);
        text-align: center;
    }

    &__right-reset {
        cursor: pointer;
        background: none rgb(255, 222, 89);
        border: none;
        text-align: center;
        font-family: Roboto;
        font-size: 12px;
        font-weight: 500;
        color: rgb(0, 0, 0);
        line-height: 1;
        border-radius: 3px;
        width: 90px;
        height: 20px;
    }
    &__block-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
    }
    &__right-btn {
        background-color: $green;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-family: Roboto;
        font-size: 20px;
        color: rgb(255, 255, 255);
        text-align: center;
        border: none;
        width: 100%;
        margin: 10px 0;
    }
    input {
        box-sizing: border-box;
        z-index: 22;
        height: 50px;
        width: 100%;
        border-style: solid;
        border-width: 1px;
        border-color: rgb(230, 230, 230);
        border-radius: 3px;
        margin: 10px 0;
        padding-left: 10px;
        &::-webkit-calendar-picker-indicator {
            opacity: 0;
        }
    }
    select {
        width: 100%;
        height: 50px;
        border: none;
        background-color: transparent;
        color: rgb(156, 156, 156);
        font-family: Roboto;
        font-size: 14px;
        border-radius: 3px;
        border-style: solid;
        border-width: 1px;
        padding-left: 10px;
        padding-right: 10px;
        border-color: rgb(230, 230, 230);
        margin: 10px 0;
    }
    svg {
        transition: fill 200ms ease 0s;
    }
    &__block-menu {
        display: flex;
        align-items: center;
        margin-top: 10px;
        cursor: pointer;
        &:hover {
            .content__block-menu-text {
                color: rgb(255, 109, 105);
            }
            svg {
                fill: rgb(255, 109, 105);
            }
        }
    }

    &__block-menu-text {
        margin-left: 5px;
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        color: rgb(41, 41, 41);
        line-height: 1.4;
        padding-top: 2px;
        border-radius: 0px;
        transition: color 200ms ease 0s;
    }

    &__latest {
        margin-top: 50px;
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px;
    }

    &__latest-items {
        width: 100%;
        display: flex;
        gap: 5px;
    }

    &__latest-img {
        max-width: 110px;
        @media (max-width: 800px) {
            max-width: 100%;
        }
    }

    &__latest-right {
    }

    &__latest-title {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 700;
        color: rgb(41, 41, 41);
        line-height: 1.4;
        margin-bottom: 10px;
    }

    &__latest-undertitle {
        font-family: Roboto;
        font-weight: 500;
        font-size: 14px;
        color: rgb(93, 100, 117);
        margin-bottom: 10px;
    }

    &__latest-price {
        font-family: Roboto;
        font-size: 14px;
        color: rgb(93, 100, 117);
        font-weight: 500;
        span {
            font-weight: 900;
        }
    }
}
